import { useState } from "react";
import Modal from "react-modal";

import "./styles.css";

import duck from "./image/duck_click.png";
import duck_sound from "./sound/duck_sound.wav";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    maxWidth: 300,
    borderRadius: 15
  }
};

function Paperella() {
  let sound = new Audio(duck_sound);
  function playSound() {
    sound.play();
    const timer = setTimeout(() => {
      window.open(
        "https://www.eventbrite.it/e/biglietti-sangria-pool-party-165721002981"
      );
      clearTimeout(timer);
    }, 500);
  }
  return (
    <div className="Paperella">
      <button onClick={() => playSound()} className="PaperellaLink">
        <img className="PaperellaImg" src={duck} alt="paperellaparty" />
      </button>
    </div>
  );
}

